@value makerGreen,makerWhite from "/src/common/colors.css";

.head {
  position: sticky;
  top: 0px;
  width: 100%;
  text-align: center;
  padding: 1rem;
  background-color: makerGreen;
  color: makerWhite;
}

.logo {
  width: 3rem;
}

.title {
  font-size: 2rem;
  margin: 0;
}

.logout {
  position: absolute;
  top: 1rem;
  right: 1rem;
  padding: 0.5rem 0.3rem;
  outline: none;
  border: 1px solid black;
  color: makerWhite;
  background-color: black;
  border-radius: 0.2rem;
}
