@value makerLightGrey,makerLightPink,makerBlack from "/src/common/colors.css";

.container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button {
  width: 100%;
  height: 100%;
  border: none;
  color: makerBlack;
  font-weight: bold;
  cursor: pointer;
  padding: 0.5rem;
}

.button.pink {
  background-color: makerLightPink;
}

.button.grey {
  background-color: makerLightGrey;
}

.button:hover {
  opacity: 0.8;
}

.input {
  display: none;
}

.loading {
  animation: spin 2s linear infinite;
}
@keyframes spin {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
